<template>
    <div class="Order-details">
        <ul class="routes">
            <li>
                <router-link to="/">首页</router-link>
            </li>
            <li>
                <router-link to="/order">订单中心</router-link>
            </li>
            <li>
                <router-link to="/order/detail">订单详情</router-link>
            </li>
        </ul>
        <div class="details">
            <div class="order-detail">
                <table>
                    <tr>
                        <td class="order-num ac">订单编号：{{this.order_info.order_sn}}</td>
                        <td rowspan="2" class="order-info">
                            <div>
                                <p>订单编号：{{this.order_info.order_sn}}</p>
                                <p>下单时间：{{this.order_info.add_time | format}}</p>
                                <p>买家留言：{{this.order_info.note}}</p>
                                <p>收货人：{{this.order_info.shouhuo_name}} 手机号码：{{this.order_info.shouhuo_moblie}}</p>
                                <p>地址：{{this.order_info.shouhuo_address}}</p>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="order-control ac">
                            <div>
                                <p class="status bold">
                                    {{order_info.status | order_status(order_info.pay_status, order_info.shipping_status)}}
                                </p>
                                <div class="control">
                                    <a v-if="order_info.status == 1 && order_info.shipping_status == 2 && order_info.pay_status == 1" @click="comfirmReceive(order_info.order_id)">确认收货</a>
                                </div>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
            <div class="goods-detail">
                <table>
                    <thead>
                    <tr>
                        <td class="ac">商品详情</td>
                        <td class="ac" style="width: 120px">单价</td>
                        <td class="ac" style="width: 120px">数量</td>
                        <td class="ac" style="width: 220px">状态</td>
                    </tr>
                    </thead>
                    <tbody>

                    <tr v-for="item in order_info.goods_child">
                        <td>
                            <img class="fl" :src="item.default_img | img" alt="">
                            <p class="fl">
                                {{item.product_name}}
                            </p>
                        </td>
                        <td class="ac">{{item.jifen | change_unit}}{{storage.getStorage('exchange').unit}}</td>
                        <td class="ac">{{item.nums}}</td>
                        <td class="ac">{{order_info.status | order_status(order_info.pay_status, order_info.shipping_status)}}
                        </td>
                    </tr>
                    </tbody>
                </table>

            </div>
            <div class="wakai">
                <ul>
                    <!--<li><p class="ar">商品总价：{{order_info.subtotal|change_unit}}{{storage.getStorage('exchange').unit}}</p></li>-->
                    <!--<li><p class="ar">优惠金额：0</p></li>-->
                    <li><p class="ar">运费：{{order_info.express_fee|change_unit}}{{storage.getStorage('exchange').unit}}</p></li>
                    <li><p class="ar">总&nbsp;&nbsp;价：<span class="Highlight">{{order_info.subtotal | change_unit}}</span>  <span>{{storage.getStorage('exchange').unit}}</span></p></li>
                </ul>
            </div>
        </div>
        <div class="pay" v-if="order_info.pay_status == 0&&order_info.shipping_status==0&&order_info.status==1">
            <div class="pay-btn bold fr ac cursor" @click="goToPay">
                立即支付
            </div>
        </div>
        <div class="pay" v-if="order_info.pay_status == 1&&order_info.shipping_status==2&&order_info.status==1">
            <div class="pay-btn bold fr ac cursor" @click="comfirmReceive(order_info.order_id)">
                确认收货
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
    export default {
        name: 'OrderStatus',
        metaInfo: {
            title: '订单详情',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        data() {
            return {
                step: 1,
                order_info:{},
                return_goods:[]
            }
        },
        mounted(){
            this.getOrderDetail()
        },
        methods: {
            getOrderDetail(){
                let order_sn = this.$route.query.order_sn || null;
                if (typeof (this.$route.query.order_sn) === "string"){
                    order_sn = [this.$route.query.order_sn]
                }

                this.yum('api/order/getOrderDetail' , {order_sn:order_sn}).then(res => {
                    if (res.data.status === 200) {
                        this.order_info = res.data.data[0];
                        /*for (var i in this.order_info.goods_child){
                            if (this.order_info.goods_child[i].id == this.$route.query.id ){
                                this.return_goods = this.order_info.goods_child[i];
                                break;
                            }
                        }

                        // 计算当前退款状态
                        if(this.return_goods.return_status == 0){
                            this.step = 1;
                        }else if (this.return_goods.return_status == 1){
                            this.step = 2;
                        }else{
                            this.step = 3;
                        }*/
                    }
                });
            },
            goToPay(){
                this.$router.push({
                    path:'/paytable',
                    query:{
                        order_sn:this.order_info.order_sn
                    }
                })
            },
            comfirmReceive(id){
                this.yum('api/Order/confirmReceive',{order_id:id}).then(res=>{
                    this.bus.$emit('notice',res.data.msg)
                })
            }
        },
        computed: {
            OrderStatus() {
                switch (this.step) {
                    case 1:
                        return '待支付';
                    case 2:
                        return '待发货'
                }
            }
        },
        created() {

            this.$store.dispatch('layout/Change', display);
        }
    }
</script>

<style lang="stylus" scoped>
    .Order-details
        min-width 100%
        margin 40px 0 0 0
        position relative
        &.router
            margin-left 0
    ul.routes
        position relative
        li
            position relative
            display inline-block
            &:after
                content '>'
                margin 0 10px
            &:last-child:after
                display none

    .details
        padding 20px
        min-height 450px
        box-shadow 0 0 8px #e4e4e4
        margin 20px auto 70px
        .order-detail
            position relative
        .goods-detail
            position relative
            margin-top 20px
            thead
                height 40px
                td
                    height 40px
            tbody
                position relative
                td
                    padding 20px
                    p
                        width 350px
                        margin-left 20px
                    img
                        width 60px
                        height 60px
                        display inline-block
        table
            width 100%
            border solid 1px #ccc
            td
                border 1px solid #ccc
            td.order-num
                width 250px
                height 40px
                background-color #f2f2f2
            td.order-control
                height 150px
                p.status
                    margin-bottom 20px
                    color #ff0077
                    font-size 18px
                div.control > a
                    padding 2px 17px
                    border 1px solid #ff0077
                    display inline-block
                    color #ff0077
                    vertical-align middle
                    &:hover
                        background-color #ff0077
                        color white
            td.order-info
                padding 0 20px
                p
                    margin-bottom 10px

    .wakai
        position relative
        margin-top 40px
        li
            margin-bottom 20px
            span.Highlight
                font-size 20px
                font-weight bold
                color #ff0077

    .pay
        height 60px
        line-height 60px
        margin-bottom 40px
        .pay-btn
            width 120px
            height 60px
            background-color #ff0077
            color white
            font-size 20px
</style>
